main {
  background-color: #efefef;
  width: 27%;
  margin: 120px auto 50px;
  border-radius: 20px;
  padding: 7px 11px;
  box-sizing: border-box;
}

h2 {
  text-align: center;
}

.register {
  text-align: left;
  margin: 20px 0 35px 10px;
  font-size: 15px;
  font-weight: 300;
}

.signupInput input,
.signupInput input[type="password"] {
  /* display: block; */
  width: 93%;
  height: 29px;
  border-radius: 10px;
  border: 1px solid rgb(204, 199, 199);
  outline: none;
  margin-bottom: 3px;
}

.signupInput {
  margin: 20px 5% 5px;
  width: 97%;
}

div select {
  height: 33px;
  border-radius: 5px;
  width: 9rem;
}

div label {
  font-size: 0.7rem;
}

button {
  height: 40px;
  width: 120px;
  border-radius: 10px;
  font-size: 18px;
  padding: 5px 10px;
  cursor: pointer;
  outline: none;
  background-color: #fed440;
  outline: none;
  border: 1px solid black;
}

.submit button:hover {
  background-color: darkorange;
}

.submit {
  margin-top: 20px;
  padding: 20px 0 40px;
  text-align: center;
}

.signupInput .errorInput {
  border: 1px solid red;
  box-shadow: 1px 1px 1px rgba(255, 0, 0, 0.46);
}

@media (max-width: 500px) {
  main {
    width: 80%;
    margin: 120px 0 50px 45px;
  }
}
