map{
  margin: 0;
  width: 260px;
  height: 320px;
}


.map .search {
    position: absolute;
    top: 1rem;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    max-width: 400px;
    z-index: 5;
  }
  
  .map .search input {
    margin: 0;
    padding: 0.5rem;
    font-size: 1.5rem;
    width: 100%;
    height: 18px;
    border-radius: 10px;
    border: 1px solid black
  }

  .map button{
    position: absolute;
    z-index: 5;
    right: 0;
    margin: 10px 10px 0 0;
  }


    #start {
      border-top-right-radius: 0px;
      border-bottom-right-radius: 0px;

    }

    #end {
      border-top-left-radius: 0px;
      border-bottom-left-radius: 0px;

    }


  .backBtn { height: 50px;
    width: 50px;
    margin: 7px;
    padding: 0;
    z-index: 5;
    position: absolute;
  }

  .backBtnImage {
    height: 50px;
    width: 50px;
  }

  .myLocationLogo {
    position: absolute;
    z-index: 5;
    height: 20px;
    width: 20px;
    border-radius: 10px;
    right: 5px;
    bottom: 5%;
    margin-right: 5px;
  
  }

  .map .popDown {
    display: flex;
    flex-direction: row;
  }

  button.ownLocation {
    bottom:5px;
    border-radius:25px;
    width:30px;
    padding: 0 7px;
  }

  button.requestRide {
    bottom: 0px;
     margin: 10px 40% 0;
      width: 250px; 
  }

@media (max-width: 500px) {

  .map .search {
    width: 55%;
  }

  .map .search input{
    font-size: 15px;
    text-align: center;
  }

  .backBtnImage {
    height: 45px;
    width: 45px;
  }

  button.destination  {
      visibility: hidden;
    }

  button.destination:after {
    margin: 0px;
    right: 5px;
    top: 0px;
    content:'D'; 
    padding: 4px 11px;
    position: absolute;
    visibility: visible;
    border-radius: 25px;
    border: 1px solid black;
    background-color: #FED540;
  }

  button.requestRide {
    bottom: 0px;
     margin: 10px 40% 0;
      width: 140px; 
  }
  
}

