.toolbar {
   left: 0px;
   top: 0px;
   width: 100%;
   height: 70px;
   background-color: white;
   position: fixed;
   display: flex;
   justify-content: space-between;
   align-items: center;
}

a {
   text-decoration: none;
   color: black;
}

.toolbar .left-nav {
   margin-top: 30px;
   font-weight: bold;
   font-size: 23px;
}

.rightNav {
   display: flex;
   margin: 30px 75px 0 0 ;
   font-size: 15px;
}



@media ( max-width:500px) {
   .toolbar .rightNav {
       margin: 20px 40px 0 0  ;
   }

   .toolbar .left-nav {
      margin-top: 20px;
      font-size: 20px;
   }
   
}



















