html{
    background: white;
    font-size: 15px;
  }

.home{
    color: #173E40;
    margin: 90px 0 20px 80px;
}

.home .rightAlign {
    float: right;
    margin: 0 80px 90px 0;
}


.home .description {
    text-align: left;
    margin: 45px 90px 70px 0;
    padding:10px 0 10px;
    width: 300px;
    height: 260px;
}

.description h1 {
    font-size:50px;
    margin-bottom:0;
}

.home .backgroundImage {
    margin: 50px 15px 0 30px;
    height: 425px;
    width: 575px;
}

.home .ashishQuote {
    height: 110px;
    width: 350px;
    margin: 10px 0;
}


.home button {
    background-color: #16a3b3;
    border: none;
}

.home .search{
    border: none;
    text-align: center;
    padding: 10px;
    border-radius: 10px;
}

.home .search input{
    outline: none;
    font-size: 40px;
}

p .toSignup {
    height: 0px;
    width: 0px;
    padding: 0px;
    margin-left: 0;
    color: #56c3a3;
    text-decoration: underline;
}

.searchRideButton {
    
    background-color: #56c3a3;
    font-size: 17px;
    font-weight: 500;
    padding: 15px 70px;
    border-radius: 3px;
    right: 0px;
}

@media (max-width: 1200px)  {
    
    .home .backgroundImage {
        margin: 50px 15px 0 20px;
        height: 300px;
        width: 450px;
    }

    .description {
        margin: 50px 50px 35px 0;

    }

    .home .rightAlign {
        margin-right: 50px;
    }

    .home .ashishQuote {
        height: 90px;
        width: 300px;
        margin: 10px 0;
    }

}

@media (max-width: 500px)  {
    
    .home .backgroundImage {
        margin: 30px auto 0 ;
        height: 200px;
        width: 280px;

    }
    
    .home .description {
        margin: 20px 0px  50px 0;
        height: 180px;

    }

    .description h1 {
        font-size: 31px;
    }

    .home .rightAlign {
        margin-right: 50px;
    }

    .home .ashishQuote {
        height: 75px;
        width: 240px;
        margin: 20px 0 10px;
    }

}